import React, {useEffect , useRef , useState} from "react";
import proj4 from "proj4"
import { useLocation } from "react-router-dom"

const { kakao } = window;

let mapStyle = {
    width: '100vw',
    height: '100vh',
    position: 'relative',
    zIndex : '1'
}

let customOverlayBackgroung = {
    width: '250px',
    height: '300px',
    backgroundColor: 'red'
}


const MapContainer = () => {
   
    // *** current location tracking ***
    var geoOptions = {
        enableHighAccuracy: true,
        timeout: 5000,
        maximumAge: 1000
    };

    function error(err) {
        console.warn('ERROR(' + err.code + '): ' + err.message);
    }

    function FindLocation() {
        if( navigator.geolocation) {
            navigator.geolocation.watchPosition(MarkingCurPosition,error,geoOptions);
        } else {

        }
    }

    function MarkingCurPosition(position) {        
        const locPosition = new kakao.maps.LatLng(position.coords.latitude,position.coords.longitude);
        currentMarker.setPosition(locPosition);
    }

    // geojson parsing & change coordinate
    function InitGovernorMarker(json) {
        // json parsing
        const geoData = JSON.parse(json);

        //change coordinate      

        const wgs84 = "+proj=longlat +ellps=WGS84 +datum=WGS84 +no_defs";
        const grs80 = "+proj=tmerc +lat_0=38 +lon_0=127 +k=1 +x_0=200000 +y_0=600000 +ellps=GRS80 +units=m +no_defs"

        for( let i = 0 ; i< geoData.features.length ; i++ ){
            let coord = proj4(grs80,wgs84,[geoData.features[i].geometry.coordinates[0],geoData.features[i].geometry.coordinates[1]]);
            let latlng = new kakao.maps.LatLng(coord[1],coord[0]);
            AddMarker(latlng,geoData.features[i].properties.STD_NAM,geoData.features[i].properties.ID);
        }
    }

    // create governor marker
    function AddMarker(position , title, id) {
        const imageSize = new kakao.maps.Size(24,35);
        const markerImage = new kakao.maps.MarkerImage("https://t1.daumcdn.net/localimg/localimages/07/mapapidoc/markerStar.png",imageSize);
        
        var govMarker = new kakao.maps.Marker({
            map: map,
            position: position,
            title: title,
            image: markerImage,
            clickable: true
        });

        let content = document.createElement('div');
        content.style.width = "200px";
        content.style.height = "220px";
        content.style.backgroundColor = "white";
        content.style.flexDirection = "column";

        // *** CustomOverlay Title ***

        let titleDiv = document.createElement('div');
        titleDiv.style.width = "100%";
        titleDiv.style.height = "18%";  
        titleDiv.style.display = "flex";      

        content.appendChild(titleDiv);

        let titleText = document.createElement('span');
        titleText.appendChild(document.createTextNode(title));
        titleText.style.fontSize = "14px";
        titleText.style.marginLeft = "15px";
        titleText.style.marginTop = "8px";
        titleText.style.fontWeight = "bold";

        titleDiv.appendChild(titleText);

        // *** CustomOverlay Infomation *** 

        let infoDiv = document.createElement('div');
        infoDiv.style.width = "100%";
        infoDiv.style.height = "65%";

        content.appendChild(infoDiv);      

        let item1 = document.createElement('div');
        item1.style.width = "100%";
        item1.style.height = "19%";
        item1.style.display = "flex";  

        infoDiv.appendChild(item1);

        let item1Name = document.createElement('span');
        item1Name.appendChild(document.createTextNode("1차 압력"));
        item1Name.style.marginLeft = "11px";
        item1Name.style.width = "30%";
        item1Name.style.height = "100%";
        item1Name.style.fontSize = "13px";
        item1Name.style.fontWeight = "bold";

        item1.appendChild(item1Name);

        let item1Value = document.createElement('span');
        item1Value.appendChild(document.createTextNode("---"));
        item1Value.style.width = "65%";
        item1Value.style.height = "100%";
        item1Value.style.fontSize = "13px";

        item1.appendChild(item1Value);

        let item2 = document.createElement('div');
        item2.style.width = "100%";
        item2.style.height = "19%";
        item2.style.display = "flex"; 

        infoDiv.appendChild(item2);

        let item2Name = document.createElement('span');
        item2Name.appendChild(document.createTextNode("2차 압력"));
        item2Name.style.marginLeft = "11px";
        item2Name.style.width = "30%";
        item2Name.style.height = "100%";
        item2Name.style.fontSize = "13px";
        item2Name.style.fontWeight = "bold";

        item2.appendChild(item2Name);

        let item2Value = document.createElement('span');
        item2Value.appendChild(document.createTextNode("---"));
        item2Value.style.width = "65%";
        item2Value.style.height = "100%";
        item2Value.style.fontSize = "13px";

        item2.appendChild(item2Value);        

        let item3 = document.createElement('div');
        item3.style.width = "100%";
        item3.style.height = "19%";
        item3.style.display = "flex"; 

        infoDiv.appendChild(item3);

        let item3Name = document.createElement('span');
        item3Name.appendChild(document.createTextNode("필터 차압"));
        item3Name.style.marginLeft = "11px";
        item3Name.style.width = "30%";
        item3Name.style.height = "100%";
        item3Name.style.fontSize = "13px";
        item3Name.style.fontWeight = "bold";

        item3.appendChild(item3Name);

        let item3Value = document.createElement('span');
        item3Value.appendChild(document.createTextNode("---"));
        item3Value.style.width = "65%";
        item3Value.style.height = "100%";
        item3Value.style.fontSize = "13px";

        item3.appendChild(item3Value); 

        let item4 = document.createElement('div');
        item4.style.width = "100%";
        item4.style.height = "19%";
        item4.style.display = "flex"; 

        infoDiv.appendChild(item4);

        let item4Name = document.createElement('span');
        item4Name.appendChild(document.createTextNode("안정성점수"));
        item4Name.style.marginLeft = "11px";
        item4Name.style.width = "30%";
        item4Name.style.height = "100%";
        item4Name.style.fontSize = "13px";
        item4Name.style.fontWeight = "bold";

        item4.appendChild(item4Name);

        let item4Value = document.createElement('span');
        item4Value.appendChild(document.createTextNode("---"));
        item4Value.style.width = "65%";
        item4Value.style.height = "100%";
        item4Value.style.fontSize = "13px";

        item4.appendChild(item4Value); 

        let item5 = document.createElement('div');
        item5.style.width = "100%";
        item5.style.height = "19%";
        item5.style.display = "flex"; 

        infoDiv.appendChild(item5);    

        let item5Name = document.createElement('span');
        item5Name.appendChild(document.createTextNode("이벤트 현황"));
        item5Name.style.marginLeft = "11px";
        item5Name.style.width = "30%";
        item5Name.style.height = "100%";
        item5Name.style.fontSize = "13px";
        item5Name.style.fontWeight = "bold";

        item5.appendChild(item5Name);

        let item5Value = document.createElement('span');
        item5Value.appendChild(document.createTextNode("---"));
        item5Value.style.width = "65%";
        item5Value.style.height = "100%";
        item5Value.style.fontSize = "13px";

        item5.appendChild(item5Value); 

        // *** CustomOverlay Button ***

        let buttonDiv = document.createElement('div');
        buttonDiv.style.width = "100%";
        buttonDiv.style.height = "17%";
        buttonDiv.style.display = "flex";   
        buttonDiv.style.justifyContent = "center";

        content.appendChild(buttonDiv);

        let modelBtn = document.createElement('button');
        modelBtn.appendChild(document.createTextNode('정압기 모델'));
        modelBtn.style.margin = "5px";
       
        modelBtn.onclick = function() {
            document.location.href = "unity:gov_click" + id;
        };

        buttonDiv.appendChild(modelBtn);

        let surroundBtn = document.createElement('button');
        surroundBtn.appendChild(document.createTextNode('360 뷰어'));
        surroundBtn.style.margin = "5px";
       
        surroundBtn.onclick = function() {
            document.location.href = "unity:surroundView_" + id;
        };

        buttonDiv.appendChild(surroundBtn);

        
        let customOverlay = new kakao.maps.CustomOverlay({
            position: govMarker.getPosition(),
            content: content,
            yAnchor: 1.2
        });

        kakao.maps.event.addListener(govMarker,'mouseover',function() {
            customOverlay.setMap(map);
        });

        // kakao.maps.event.addListener(govMarker,'mouseout',function() {
        //     setTimeout(function() {
        //         customOverlay.setMap();
        //     });
        // });

        
    }   


    // map element
    let map; 
    // current position marker element
    let currentMarker;
    // current position tracking start valiable
    let start;

    // *** INIT KAKAO MAP ***
    useEffect(() => {
        const container = document.getElementById('digitaltwinMap');
		const options = {
			center: new kakao.maps.LatLng(36.393250, 127.405276),
			level: 3
		};
        map = new kakao.maps.Map(container, options);

        const initPosition = new kakao.maps.LatLng(36.393250, 127.405276);
        currentMarker = new kakao.maps.Marker({
            position: initPosition,
            clickable: true
        });
        currentMarker.setMap(map);

        fetch('https://sinjin0.github.io/WebpageTest/govdata.geojson')
        .then(function(response) {
            return response.json();
        })
        .then(function(json) {
            InitGovernorMarker(JSON.stringify(json));
        });

        start = FindLocation();
        
        console.log("Update Init");

    }, []);

    return (
        <div id='digitaltwinMap' style={mapStyle}></div>
    );
}

export default MapContainer;



