import React, {useState , useEffect} from 'react'
import "./Button.scss"
import "./SidePanel.scss"


const MapUserInterface = () => {
    const [isActive,setState] = useState(0);


    function SideMenuClick(e) {
        if( isActive === 0 ) {
            setState(1);
        }
        else {
            setState(0);
        }
    }

    const MenuInterface = (props) => {
        return (
            <div className={props.type} >
                <lable className={"labelStyle"} >네트워크가 원활하지 않습니다.</lable>
                <button className={"loginButton"}>Logout</button>
                {/* <label for="toggle" class={"menuPanel-toggleSwitch"}>
                    <span class={"menuPanel-toggleButton"}></span>
                </label>
                <h3>유성구 Patrol</h3>
                <h1>홍길동</h1> */}
                <label className={"labelStyle2"} >유성구 Patrol</label>
                <label className={"labelStyle3"} >
                    홍길동
                    <label className={"labelStyle4"}>안전 기술 팀</label>
                </label>
                <label className={"labelStyle5"} >
                    GPS 설정
                    <label class={"label toggle"}>
                        <input type="checkbox" class={"toggle_input"} />
                        <div class={"toggle-control"}></div>
                    </label>
                </label>
                <button className={"menuPanel-exitButton"} onClick={SideMenuClick}></button>
            </div>
        )    
    }

    // useEffect(()=>{ 
    //     if( isActive === 0 ) {
    //         DeactiveSideMenu();
    //     }
    //     else {
    //         ActiveSideMenu();
    //     }
    // },[]);  
    
    
    return (       
        <div>
            <button className={ isActive === 0 ? "activeSideMenuButton" : "dectiveSideMenuButton"} onClick={SideMenuClick}></button>
            <MenuInterface type={ isActive === 0 ? "menuPanelDeactive" : "menuPanelActive"}></MenuInterface>
        </div>       
    )
}

export default MapUserInterface;