import React, {useState , useEffect} from 'react'
import "./Button.scss"
import "./SidePanel.scss"

const GovernorInfomation = () => {
    const [isActive,setState] = useState(0);

    function SideMenuClick(e) {
        if( isActive === 0 ) {
            setState(1);
        }
        else {
            setState(0);
        }
    }

    const GovernorPanelAppear = (props) => {
        return (
            <div className={props.type} >
                <div className={'test'} style={{display:'flex'}} >
                    <h3 style={{marginTop:20 , marginLeft:50}}>정압기 목록</h3>
                    <button style={{marginTop:20 , marginLeft:140 , width:200}}>현재 위치 기준 정렬</button>
                    <button style={{margin:20 ,marginLeft:20 , width:180}}>내림 차순</button>
                </div>
                <div>
                    <textarea style={{width: 650 , marginTop:30 , height:50}}></textarea>
                </div>
               
            </div>
        )    
    }
    
     
    return (       
        <div>
            <button className={isActive === 0 ? "activeGovernorInfoButton" : "deactiveGovernorInfoButton"} onClick={SideMenuClick}></button>
            <GovernorPanelAppear type={isActive === 0 ? "governorInfoPanelDeactive" : "governorInfoPanelActive"}></GovernorPanelAppear>
        </div>       
    )
    

} 

export default GovernorInfomation;
